class auth{

    constructor(Axios){
        this.axios = Axios
        this.url = '/auth'
    }
    
    login(data) {
        return this.axios.post(`${this.url}/login`, data);
    }
    changePassword(data){
        let url = `${this.url}/password`
        return this.axios.post(url, data);
    }
}

export default auth