class session {
    constructor(){

    }
    setKey(name,value){
        sessionStorage.setItem(name,JSON.stringify(value));
    }
    getKey(key){
        let data =  sessionStorage[key];
        if (data == ""){
           return undefined;
        }else {
            return JSON.parse(data);
        }
    }
    removeKey(key){
        sessionStorage.removeItem(key)
    }
    hastKey(key){
        let data = sessionStorage[key];
        if(data === undefined){
            return false;
        }else{
            return true;
        }
    }
}
export default session.prototype