import Axios from 'axios'
import session from '../lib/session'

//Axios.defaults.baseURL = 'https://192.168.42.30/api/';
//Axios.defaults.baseURL = 'http://lab.test/api/v1';
Axios.defaults.baseURL = 'https://labglezapp.herokuapp.com/api/v1'

Axios.interceptors.request.use((c)=>{
    if (session.hastKey('session_lab')){
        const token = session.getKey('session_lab');
        if (token){
            c.headers.Authorization = token.api_token;
        }
    }
    return c
},(err)=>{
    return Promise.reject(err)
});
window.base_url = Axios.defaults.baseURL;
import auth from './auth'
import users from './users'
import clientes from './clientes'
import productos from './productos'
import comprobante from './comprobante'
import lab from './laboratorio'
import comprobanteDetalle from './comprobanteDetalle'



export default {
    auth: new auth(Axios),
    users: new users(Axios),
    clientes: new clientes(Axios),
    productos: new productos(Axios),
    ventas: new comprobante(Axios),
    lab: new lab(Axios),
    detalle: new comprobanteDetalle(Axios),
}