class clientes{

    constructor(axios){
        this.axios = axios
        this.url = '/clientes'
    }

    getAll(){
        return this.axios(`${this.url}?all=${true}`);
    }

    paginate(p){
        return this.axios.get(this.url,{params:p.removeKeysEmptys()});
    }

    get(id){
        return this.axios.get(`${this.url}/${id}`);
    }
    search(model){
        return this.axios.post(`${this.url}/search`,model);
    }
    ventas(model){
        return this.axios.post(`${this.url}/ventas/search`,model);
    }
    save(data){
        return this.axios.post(this.url,data);
    }

    update(data){
        return this.axios.put(`${this.url}/${data.id}`,data);
    }

    destroy(id){
        return this.axios.delete(`${this.url}/${id}`);
    }
}

export default clientes