const toast =  require('toastr');
class Toast {
    constructor(){}
    error(data){
        var msj = 'Por favor, complete los campos..!';
        if(typeof(data) !== 'undefined'){
            msj = data.msj;
        }
        return toast.error(msj,'Error',{closeButton: true});
    }
    info(data){
        var msj = 'Registro actualizado con éxito!';
        if(typeof(data) !== 'undefined'){
            msj = data.msj;
        }
        return toast.info(msj,'Atencion!',{closeButton: true});
    }
    success(data){
        var msj = 'Registro guardado con éxito!';
        if(typeof(data) !== 'undefined'){
            msj = data.msj;
        }
        return toast.success(msj,'Éxito',{closeButton: true});
    }
    warning(data){
        var msj = 'Registro eliminado con exito!';
        if(typeof(data) !== 'undefined'){
            msj = data.msj;
        }
        return toast.warning(msj,'Atencion!',{closeButton: true});
    }
    catch(data){
        var msj = 'Ocurrio un error inesperado. :(';
        if(typeof(data) !== 'undefined'){
            msj = data.msj;
        }
        return toast.error(msj,'Error',{closeButton: true});
    }
}
export default Toast
