<template>
    <span v-if="loading">
        <i class="fa fa-circle-o-notch fa-spin"></i>
    </span>
    <button v-else :type="type" :class="color">
        <span v-if="label!==''">{{label}} </span>
        <i v-if="icon!=='' && !loading" :class="`fa ${icon}`"/>
        <span v-if="loading">
            <i class="fa fa-circle-o-notch fa-spin"></i>
        </span>
    </button>
</template>

<script>
    export default {
        name: "v-btn",
        props: {
            color:{
                type: String,
                required: false,
                multiple:true,
            },
            type:{
                type: String,
                default:'button',
                required: false
            },
            loading: {
                type: Boolean,
                default:false,
                required: false
            },
            icon: {
                type: String,
                required: false,
            },
            label: {
                type: String,
                required: false,
            },
        },
    }
</script>