<template>
    <div class="col-xs-12 col-md-6 col-md-offset-3">
        <div class="panel panel-default">
            <div class="panel-heading">Inicio de sesión</div>
            <div class="panel-body">
                <div class="login-box-body">
                    <form id="form" @submit.prevent="onSubmit" @reset.prevent="onReset">
                        <ValidationObserver ref="form">
                            <ValidationProvider vid="email" name="email" rules="required|email" v-slot="{errors}">
                                <div class="form-group has-feedback" :class="{'has-error':errors.length==1}">
                                    <label>Usuario</label>
                                    <input v-model="user.email" type="text" class="form-control" name="user" required autofocus autocomplete="false">
                                    <span class="fa fa-user form-control-feedback"></span>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="password" name="password" rules="required" v-slot="{errors}">
                                <div class="form-group has-feedback" :class="{'has-error':errors.length==1}">
                                    <label>Contraseña</label>
                                    <input v-model="user.password" :type="showPassword?'text':'password'" class="form-control" name="password" required autocomplete="false">
                                    <span class="fa fa-lock form-control-feedback"></span>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                           <div class="form-group">
                               <label class="help-block">
                                   <input type="checkbox" v-model="showPassword"> Mostrar contraseña
                               </label>
                           </div>
                        </ValidationObserver>
                        <button type="submit" class="btn btn-primary btn-block btn-flat">
                            <span class="fa fa-sign-in"></span> Entrar
                        </button>
                    </form>
                </div>
            </div>
           <div class="panel-footer">

           </div>
        </div>
    </div>
</template>

<script>
    import api from '@/services'
    import {AbilityBuilder} from '@casl/ability'
    export default {
        name: "login",
        data(){
            return {
                type:'text',
                send:false,
                btn:false,
                showPassword:false,
                user:{
                    email:'',
                    password:''
                }
            }
        },
        mounted(){
          this.$emit('title','Inicio de sessión');
        },
        methods:{
            onSubmit(){
                this.$refs.form.validate().then((success)=>{
                    if (success){
                        this.send = true;
                        this.login();
                    }
                });
            },
            onReset () {
                this.$refs.form.reset();
            },
            login(){
               return api.auth.login(this.user).then((r)=>{
                    if (r.data.status === 200){
                        this.$session.setKey('session_lab',r.data.data);
                        window.key = r.data.data.api_token;
                        this.updateAbilityBuilder(r.data.data.user_type)
                        this.$router.replace('/home');
                    }
                });
            },
            updateAbilityBuilder(type){
                const {can,rules} = new AbilityBuilder();
                if(type===5){
                    can('admin')
                }else if (type===1){
                    can('ventas');
                }else if (type==2){
                    can('lab');
                }
                this.$ability.update(rules);
            }
        }
    }
</script>
