<template>
    <div class="box box-primary">
        <div class="box-header">
            <h3 class="box-title">{{title}}</h3>
            <div v-if="!btns" class="box-tools">
                <div class="btn-group">
                    <button v-if="!btnActualizar" @click="refresh" type="button" class="btn btn-primary btn-sm">
                        Actualizar <span class="fa fa-refresh"></span>
                    </button>
                    <button v-if="!btnBuscar" @click="submit" type="button" class="btn btn-success btn-sm">
                        Buscar <span class="fa fa-search"></span>
                    </button>
                </div>
            </div>
        </div>
        <div class="box-body table-responsive no-padding">
            <slot name="table"></slot>
        </div>
       <div v-if="!footer" class="box-footer">
           <div class="row">
               <div class="col-sm-4">
                   <div class="row">
                       <div class="col-xs-3 text-right">
                           <label>Por pagina:</label>
                       </div>
                       <div class="col-xs-4">
                           <select @change="submit" v-model="opt.per_page" class="form-control input-sm">
                               <option v-for="(l,i) in limite" :value="l" :key="i">{{l}}</option>
                           </select>
                       </div>
                   </div>
               </div>
               <div class="col-sm-4 text-center" style="display: flex;align-items: center;">
                   <span @click="first" class="btn" title="Primera página"><span class="fa fa-fast-backward"></span></span>
                   <span @click="previous" class="btn" title="Página anterior"><span class="fa fa-step-backward"></span></span>
                   <span><b>Página</b></span>
                   <input v-model="opt.page" type="number" class="form-control input-sm" min="1" style="width: 60px">
                   <span>de <b>{{ pages }}</b></span>
                   <span @click="next" class="btn" title="Página siguiente"><span class="fa fa-step-forward"></span></span>
                   <span @click="last" class="btn" title="Última página"><span class="fa fa-fast-forward"></span></span>
               </div>
               <div class="col-sm-4 text-right">
                   <span><b>{{total}}</b> Registros encontrados</span>
               </div>
           </div>
       </div>
    </div>
</template>

<script>
    export default {
        name: "c-data-tables",
        props:{
            title:{
                type:String,
                default:'',
                required:false
            },
            limite:{
                type:Array ,
                required: false,
                default: ()=> [10,20,50,100]
            },
            total:{
              type: Number,
              required: false,
              default: 0
            },
            opt:{
                type: Object,
                required: false,
            },
            btnBuscar:{
                type: Boolean,
                required: false
            },
            btnActualizar:{
                type: Boolean,
                required: false
            },
            btns:{
                type: Boolean,
                required: false
            },
            footer: {
                type:Boolean,
                default:true
            }
        },
        mounted(){
            this.opt.per_page = this.limite[0];
            this.$emit('paginate',this.opt);
        },
        data(){
            return {
                pages:1,
                opts:{
                    per_page:10,
                    page:1,
                }
            }
        },
        methods: {
            calcularPaginas(){
              this.pages = Math.ceil(this.total / this.opt.per_page);
            },
            submit(){
              this.$emit('paginate',this.opt);
            },
            first(){
              if (this.pages > 1) {
                  this.opt.page = 1;
                  this.submit();
              }
            },
            last(){
                if (this.opt.page < this.pages){
                    this.opt.page = this.pages;
                    this.submit();
                }

            },
            next(){
                if (this.opt.page < this.pages) {
                    this.opt.page += 1;
                    this.submit();
                }
            },
            previous(){
                if (this.opt.page > 1) {
                    this.opt.page -= 1;
                  this.submit();
                }
            },
            refresh(){
              this.opt.per_page = 10;
              this.opt.page = 1;
              this.$emit('refresh',this.opt);
            },
        },
        watch:{
            total(){
                this.calcularPaginas();
            }
        }
    }
</script>
