<template>
    <header class="main-header">
    <nav class="navbar navbar-static-top">
      <div class="container">
        <div class="navbar-header">
          <router-link v-if="$can('admin') || $can('ventas')" to="/home" class="navbar-brand"><span class="fa fa-home"></span><b>LAB</b></router-link>
            <router-link v-else to="/login" class="navbar-brand"><span class="fa fa-medkit"></span> <b> LAB</b></router-link>
          <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar-collapse" aria-expanded="false">
            <i class="fa fa-bars"></i>
          </button>
        </div>
        <div class="navbar-collapse pull-left collapse" id="navbar-collapse" aria-expanded="false" style="height: 1px;">
          <ul class="nav navbar-nav">
            <li>
              <router-link v-if="$can('admin') || $can('ventas')" to="/caja-rapida"><span class="fa fa-shopping-cart"></span> Caja</router-link>
            </li>
            <li>
              <router-link v-if="$can('admin')" to="/home/ventas"><span class="fa fa-money"></span> Registro de Ventas</router-link>
            </li>
            <li>
              <router-link v-if="$can('admin') || $can('lab')" to="/home/laboratorio"><span class="fa fa-flask"></span> Laboratorio</router-link>
            </li>
            <li v-if="$can('admin') || $can('ventas')" class="dropdown">
              <a href="#" class="dropdown-toggle" data-toggle="dropdown" aria-expanded="false">Sistema <span class="caret"></span></a>
              <ul class="dropdown-menu" role="menu">
                <li>
                  <router-link v-if="$can('admin') || $can('ventas')" to="/clientes">Clientes</router-link>
                </li>
                <li>
                  <router-link v-if="$can('admin')" to="/home/productos">Productos</router-link>
                </li>
                <li>
                  <router-link v-if="$can('admin')" to="/home/usuarios">Usuarios</router-link>
                </li>
              </ul>
            </li>
            <li v-if="$can('admin')" class="dropdown">
              <a href="#" class="dropdown-toggle" data-toggle="dropdown" aria-expanded="false">Reportes <span class="caret"></span></a>
              <ul class="dropdown-menu" role="menu">
                <li>
                  <router-link v-if="$can('admin') || $can('ventas')" to="/reportes/ventas">Ventas</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <div class="navbar-custom-menu">
          <ul class="nav navbar-nav pull-right">
            <li><span class="navbar-brand">{{title}}</span></li>
            <li><a v-if="$can('admin') || $can('ventas') || $can('lab')" @click="logout" href="#" class="btn "><span class="fa fa-sign-out"></span>Salir</a></li>
          </ul>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
    import {AbilityBuilder} from '@casl/ability'
    export default {
        name: "HeaderTag",
        props:{
            title: String
        },
        methods: {
            logout(){
                let type = 0;
                const {can,rules} = new AbilityBuilder();
                if(type===5){
                    can('admin')
                }else if (type===1){
                    can('ventas');
                }
                window.key = null;
                this.$session.removeKey('session_lab');
                this.$ability.update(rules);
                this.$router.replace('/login');
            }
        },
    }
</script>