import { extend} from 'vee-validate';

extend('age',value => {
    if (value >0 && value < 100) return true;
    else
        return 'Ingrese una valor real';
});

extend('phone',value => {
    if (value.length == 0 || value.length == 10) return true;
    else
        return 'Ingrese un numero de 10 digitos';
});

extend('positive',value => {
    if (value >0 ) return true;
    else
        return 'Ingrese un numero mayor a 0';
});

extend('date',value => {
    if (value.length == 10) return true;
    else
        return 'Ingrese una fecha valida';
});
