<template>
    <div class="input-group">
        <input @keyup="enviar" v-model="model" :type="type" class="form-control input-sm">
        <span class="input-group-btn">
            <button @click="remove" class="btn btn-default btn-sm">
                <span class="fa fa-remove"></span>
            </button>
        </span>
    </div>
</template>

<script>
    export default {
        name: "c-search-group",
        props:{
            value:{
                type:Object,
                required:false,
                default:null
            },
            type:{
                type:String,
                required:false,
                default: 'text'
            }
        },
        data(){
          return {
              model:null
          }
        },
        methods: {
            remove(){
                this.model = null;
                this.$emit('remove');
            },
            enviar(){
                this.$emit('search');
            }
        }
    }
</script>