import {defineAbility} from '@casl/ability'
import session from '../lib/session'
export default defineAbility((can)=>{
    if (session.hastKey('session_lab')){
        const token = session.getKey('session_lab');
        if (token.user_type > 0){
            let type = token.user_type
            if(type==5){
                can('admin')
            }else if (type==1){
                can('ventas');
            }else if (type==2){
                can('lab');
            }
        }
    }
});