 //components
import login from '../components/login'
const routes = [
    { path: '/login', name: 'login', component: login },
    { path: '/home', name: 'homeAdmin', component: ()=> import('../components/home/home') },
    { path: '/home/productos', name: 'productos', component: ()=> import('../components/productos/productos') },
    { path: '/clientes', name: 'clientes', component: ()=> import('../components/clientes/clientes') },
    { path: '/caja-rapida', name: 'caja-rapida', component: ()=> import('../components/ventas/caja-rapida') },
    { path: '/home/ventas', name: 'ventas', component: ()=> import('../components/ventas/ventas') },
    { path: '/home/usuarios', name: 'usuarios', component: ()=> import('../components/usuarios/usuarios') },
    { path: '/home/laboratorio', name: 'laboratorio', component: ()=> import('../components/laboratorio/laboratorio') },
    { path: '/reportes/ventas', name: 'reportesVentas', component: ()=> import('../components/reportes/ventas') },
];

export default routes